/* Colors */
// Base colors
$black: #000;
$gray: #959595;
$soft-gray: #eeeeee;
$white: #fff;
$new-york-pink: #d27c82;
$supernova: #ffc30f;
$candlelight: #ffdc0f;
$de-york: #72c279;
$allports: #0077a4;
$cerulean: #00aff0;
$picton-blue: #40aee9;

// Brand colors
$text-color-base: $gray;
$brand-primary-color: $candlelight;
$brand-primary-darker-color: $supernova;
$color-success: $de-york;
$color-danger: $new-york-pink;
$color-blue: $picton-blue;

// Component colors
$menu-bg-color: $white;
$menu-link-text-color: $cerulean;
$menu-link-hover-text-color: $allports;
$menu-link-bg-color: $cerulean;
$menu-link-hover-bg-color: $allports;
$menu-navbar-toggle-bg-color: $cerulean;
$menu-navbar-toggle-hover-bg-color: $allports;
$menu-icon-bar-bg-color: $white;

$user-block-carousel-bg-color: $picton-blue;


/* Variables */
// Base variables
$spacer: 8px;
$border-radius-base: 5px;
$icon-font-size-base: 16px;
$ubuntu-font-family: 'Ubuntu Condensed', 'Helvetica', 'Arial', sans-serif;
$opensans-font-family: 'Open Sans', 'Helvetica', 'Arial', sans-serif;

// Component variables
$menu-font-size: 15px;
$menu-icon-font-size: $menu-font-size + 1;




