@import 'partials/_variables.scss';
@import 'partials/_mixins.scss';
// @import '~bourbon/app/assets/stylesheets/_bourbon.scss';
@import '~include-media/dist/include-media';

// Include media breakpoints
// devices viewport width starts at:
$breakpoints: (
  phone: 320px,
  tablet: 768px,
  desktop: 970px,
  widescreen: 1200px
);

// Prefixing the whole back end so we don't apply rules outside our module.
// #content to beat specifity score
#content.bootstrap .trustedshops-back {
  text-align: center;
  color: $text-color-base;
  // font-size: $icon-font-size-base;

  > * {
    text-align: left;
  }

  /**
   * Reset
   */
  h1, .h1,
  h2, .h2,
  h3, .h3,
  h4, .h4,
  h5, .h5,
  h6, .h6 {
    font-family: $opensans-font-family;
    color: $black;
  }

  img {
    max-width: 100%;
  }

  [class^="icon-"] {
    font-size: $icon-font-size-base;
  }

  [id^="icp_color_"] {
    cursor: pointer;
  }

  /**
   * Helpers
   */
  @include generate-spacing-classes();

  .pos-r {
    position: relative;
  }

  .pos-a {
    position: absolute;
  }

  .list-reset {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  .text-success {
    color: $color-success;
  }

  .text-danger {
    color: $color-danger;
  }

  .v-align {
    display: flex;
    align-items: center;
    width: fit-content;

    > [class^="icon-"] + span,
    > span + [class^="icon-"] {
      margin-left: 7px;
    }
  }

  .word-break-all {
    word-break: break-all;
  }

  .progress-status {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 11px;
  }

  .sm-input {
    width: 40px;
  }

  .emphasis {
   background: #f5f5f5;
   padding: 3px 0 6px;
   border: 1px solid #ccc;
   border-radius: $border-radius-base;
  }

  /**
   * General
   */
  hr {
    border-color: $soft-gray;
    margin: 17px -20px 27px;

    &.or {
      text-align: center;
      overflow: visible;

      &:before {
        display: inline-block;
        padding-left: 14px;
        padding-right: 14px;
        content: attr(data-text);
        background-color: #fff;
        transform: translateY(-50%);
      }
    }
  }

  .process-icon-send {
    &:before {
      content: '\f1d8';
    }
  }

  .table-container {
    max-height: 400px;
    overflow-y: auto;
  }

  .table, .css-table {
    label {
      margin: 0;
      font-weight: inherit;

      &[for] {
        cursor: pointer;
      }
    }
  }

  .css-table {
    display: table;
    width: 100%;
    color: #666;

    &.striped {
      .tbody .tr:nth-child(odd) .td,
      .tbody .tr:nth-child(odd) .th {
        background-color: #f9f9f9;
      }
    }

    &.bordered {
      border: 1px solid #ddd;
      border-radius: $border-radius-base;
      overflow: hidden;

      .th, .td {
        border-bottom: solid 1px #EAEDEF;
      }
    }

    .tbody {
      display: table-row-group;
      width: 100%;
    }

    .tr {
      display: table-row;

      &.thead {
        color: $black;

        .th {
          border-bottom: 1px solid #a0d0eb;
        }
      }
    }

    .td, .th {
      display: table-cell;

      &.center {
        padding: $spacer;
        text-align: center;
      }
    }
  }

  .panel-footer-sm {
    height: auto;
  }

  .small {
    color: $gray;
  }

  .btn {
    > [class^="icon-"]:not('.btn-icon') {
      vertical-align: middle;
      margin-right: $spacer / 2;
    }
  }

  .btn-primary {
    text-transform: none;
  }

  .btn-cta {
    color: rgba(#000, .6);
    font-weight: 600;

    background-color: $brand-primary-color;
    border-color: $brand-primary-darker-color;

    &:hover {
      color: #000;
      background-color: $brand-primary-darker-color;
    }
  }

  .btn-close {
    position: absolute;
    top: 10px;
    right: 10px;

    color: $gray;
    font-size: 18px;

    transition: all .2s ease;
    cursor: pointer;

    &:hover {
      color: darken($gray, 20%);
    }
  }

  .sub-form {
    position: relative;
    padding-top: $spacer * 4;

    &:before {
      position: absolute;
      left: -20px;
      right: -20px;
      top: 0;
      height: 1px;
      content: '';
      background-color: $soft-gray;
    }
  }

  .form-control-plaintext {
    display: inline-block;
    padding: 7px 8px;
  }

  .label-control {
    font-weight: 400;
  }

  // Color picker input
  .color-picker {
    // max-width: 160px;

    .mColorPickerTrigger {
      cursor: pointer;

      > img {
        max-width: none;
      }
    }
  }

  // advanced configuration code block
  .code-input {
    min-height: 170px;
    resize: vertical;
  }

  //Form validate
  .form-group {
    &.form-error {
      input, textarea {
        border: 1px solid #f13340;
        color: #f13340;
        background: url('../img/form-error.png') 98% 7px no-repeat #fff1f2;
      }

      .help-block {
        color: #f13340;
      }
    }

    &.form-ok {
      input, textarea {
        border: 1px solid #46a74e;
        color: #35b33f;
        background: url('../img/form-ok.png') 98% 7px no-repeat #ddf9e1;
      }
    }

    &.form-error, &.form-ok {
      .form-control {
        padding-right: 30px;
      }
    }
  }

  .modal-body {
    > iframe {
      width: 100%;
      height: auto;
      min-height: 800px;
    }
  }

  /**
   * Breadcrumb component
   */
  .breadcrumb-block {
    display: flex;
    position: relative;
    justify-content: space-between;
    margin-top: $spacer;

    &:before {
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      top: 8px;
      height: 4px;
      content: '';

      background-color: $soft-gray;
      border-radius: 2px;
    }

    > .item {
      z-index: 1;

      > .number {
        display: block;
        margin: auto;
        width: 21px;
        height: 21px;

        color: $white;
        font-size: 10px;
        font-weight: 600;
        text-align: center;

        background-color: #999999;

        border-radius: 100%;
        border-style: solid;
        border-width: 3px;
        border-color: #D2D8DB;

        &:after {
          content: attr(data-content);
        }
      }

      > .title {
        display: block;
        margin-top: $spacer * 2;
        padding: 0 ($spacer * 3);
        color: $gray;
        font-size: 14px;
      }

      &.active,
      &.complete {
        > .number {
          background-color: $cerulean;
          border-color: #BBDEF0;
        }

        > .title {
          color: $picton-blue;
        }
      }

      &.complete {
        > .number:after {
          content: '\f00c';
          font-family: 'FontAwesome';
        }
      }
    }

    > .progress-bar {
      display: block;
      position: absolute;
      left: 0;
      top: 8px;
      height: 4px;
      content: '';

      background-color: $picton-blue;
      border-radius: 2px;

      transition: none;
    }
  }

  [data-toggle-wizard] {
    cursor: help;
  }


  /**
   * Menu
   */
  .main-menu-wrapper {
    max-width: 1060px;
    margin: 0 auto;
    @include media('<widescreen') {
      text-align: center;
    }

    .navbar-toggle {
      float: left;
      margin: 0 15px 15px 0;
      background: $menu-navbar-toggle-bg-color;

      &:hover {
        background: $menu-navbar-toggle-hover-bg-color;
      }

      .icon-bar {
        background: $menu-icon-bar-bg-color;
      }
    }

    .navbar-collapse {
      padding: 0;

      .nav-collapse {
        border: 0;
      }
    }

    .main-menu {
      @extend .list-reset;
      display: inline-block;
      background: $menu-bg-color;
      margin: 0 0 15px;
      border-radius: $border-radius-base;
      font-family: $ubuntu-font-family;
      text-transform: uppercase;
      text-align: left;
      @include media('<widescreen') {
        float: none;
      }
      @include media('<tablet') {
        width: 100%;
      }

      li {
        float: left;
        @include media('<tablet') {
          float: none;
        }

        a {
          display: block;
          color: $menu-link-text-color;
          padding: 8px 12px;
          font-size: $menu-font-size;
          text-decoration: none;

          transition: background-color .2s ease,
                      border-radius .2s ease;

          &:hover {
            color: $menu-link-hover-text-color;
          }

          i {
            margin-right: 5px;
            font-size: $menu-icon-font-size;
          }
        }

        &.active {
          a {
            color: $white;
            background: $menu-link-bg-color;
            border-radius: $border-radius-base;
            // pointer-events: none;
          }
        }

        &.highlighted {
          a {
            position: relative;
            color: $white;
            background: $allports;
            border-radius: $border-radius-base;

            &:after {
              display: block;
              position: absolute;
              right: 0;
              bottom: -12px;
              width: 21px;
              height: 22px;
              content: url('../img/hand.png');
              animation: pulse .8s infinite;

              @keyframes pulse {
                0% {
                  opacity: 1;
                  transform: scale(1);
                }
                70% {
                  opacity: .5;
                  transform: scale(1.2);
                }
                100% {
                  opacity: 1;
                  transform: scale(1);
                }
              }
            }
          }
        }
      }
    }
  }

  /**
   * Layout
   */
  .content-wrapper {
    display: flex;
    flex: 1;
    justify-content: center;

    &.full-width {
      display: inline-block;
      width: 1060px;
      max-width: 100%;
    }

    @include media('<widescreen') {
      flex-flow: column;
      align-items: center;
    }

    .left-column {
      max-width: 800px;
      flex: 1;
    }

    .right-column {
      order: 1;
      margin: 0 0 0 20px;
      @include media('<widescreen') {
        margin: 0;
        text-align: center;
      }

      > .block {
        width: 240px;
        margin: 0 0 20px;
        padding: 10px;
        border-radius: $border-radius-base;
        @include media('<widescreen') {
          display: inline-block;
          vertical-align: top;
          margin: 0 10px 20px;
        }
      }

      .holder {
        max-width: 300px;
        margin: 0 auto;
      }
    }
  }

  /**
   * User Block
   */
  .user-block {
    background: $brand-primary-color;
    overflow: hidden;
    text-align: initial;

    .user-header {
      margin: 0 0 20px;

      .holder {
        > span {
          color: rgba($black, .6);

          > i {
            margin-right: $spacer;
          }
        }

        > a {
          margin-top: -10px;
          margin-right: -10px;
          margin-bottom: -10px;
          padding: 10px;
          color: $black;
          line-height: 1;

          border-radius: 5px;
          transition: all .3s ease;

          &:hover {
            text-decoration: none;
            background-color: rgba($black, .2);
          }

          &.username {
            float: left;
            font-size: 12px;
            color: lighten($black, 40%);

            &:hover {
              color: $black;
            }

            i {
              margin-right: 5px;
            }
          }

          &.logout {
            float: right;
            margin-left: 10px;

            i {
              color: $black;
            }
          }
        }
      }
    }

    .user-content {
      label {
        display: block;
        font-size: 12px;
        color: $black;
        font-weight: normal;

        i {
          font-size: 14px;
        }
      }

      select {
        margin: 0 0 15px;
      }

      button {
        width: 100%;
        justify-content: center;
        margin: 0 0 10px;
      }

      .link-wrapper {
        text-align: center;
        margin: 0 0 15px;

        a {
          font-size: 14px;
          color: $black;
          text-decoration: none;
          border-bottom: 1px solid $black;
        }
      }
    }

    .user-footer {
      background: $black;
      text-align: center;
      font-weight: bold;
      font-size: 14px;
      padding: 10px;
      margin: 0 -10px -10px;

      .trial-title {
        text-transform: uppercase;
        color: $white;
        letter-spacing: 4px;
      }

      .trial-phone {
        justify-content: center;
        margin: 0 auto;
        color: $brand-primary-color;
        text-decoration: none;

        &:hover {
          color: $brand-primary-darker-color;
        }
      }
    }
  }

  /**
   * Onboarding Block
   */
  .onboarding-block {
    padding: 20px !important;

    color: $white;
    text-align: center;
    background: $user-block-carousel-bg-color;
    border-radius: $border-radius-base !important;
    overflow: hidden;

    .icon {
      font-size: 90px;
      margin: 0 0 23px;
    }

    .header {
      font-size: 14px;
      font-weight: bold;
      color: inherit;
    }

    .description {
      max-width: 200px;
      font-size: 11px;
      margin: 0 auto 18px;
      color: inherit;
    }

    .progress-status {
      margin: 0 auto;
      color: $black;
    }

    .item {
      margin-bottom: 50px;
    }

    .carousel-indicators {
      bottom: 14px;
      margin-bottom: 0;

      li {
        width: 7px;
        height:7px;
        background: $white;
        margin: 0 5px;
        border: 0;
      }

      li.active {
        background: $black;
      }
    }

    &.finished {
      background-color: $de-york;

      .item {
        margin-bottom: 0;
      }
    }
  }

  .logo-block {
    padding: 0;
    text-align: center;
  }


  /**
   * Sign in
   */
  .panel-sign-in {
    display: inline-block;
    margin: auto;
    padding: 0;
    max-width: 972px;
    overflow: hidden;
  }

  .sign-in,
  .sign-up-trial {
    > form {
      float: left;
      width: 344px;
      padding: 38px;
      @include media('<widescreen') {
        float: none;
        margin: 0 auto;
      }
      @include media('<desktop') {
        padding-top: 65px;
      }

      > img.logo {
        margin-top: -20px;
        margin-left: -20px;
        @include media('<widescreen') {
          position: absolute;
          top: 40px;
          left: 40px;
        }
      }

      .help-block {
        font-style: inherit;
      }
    }

    > aside {
      position: relative;
      float: left;

      .bubble {
        position: absolute;
        top: 20px;
        left: 20px;
        padding-top: 14px;
        padding-left: 18px;
        width: 233px; // 164px
        height: 83px;

        font-size: 14px;
        font-weight: 600;
        color: #000;

        background-image: url('../img/aside_bubble_wide.png'); // aside_bubble.png
        background-repeat: no-repeat;

        .icon {
          margin-right: $spacer;
          font-size: 18px;
          vertical-align: middle;
        }
      }

      .info {
        position: absolute;
        left: 20px;
        bottom: 165px;
        color: $white;
        text-shadow: 0px 0px 2px rgba(0, 0, 0, .8);

        > .title {
          margin-bottom: $spacer * 2;
          font-family: $ubuntu-font-family;
          font-size: 31px;
        }

        > .baseline {
          display: block;
          max-width: 405px;
          font-size: 16px;
        }
      }
    }
  }


  /**
   * Home page
   */
  .home {
    .home-left-block {
      max-width: 450px;
      padding: 30px 60px;
      @include media('<widescreen') {
        padding: 20px;
      }
      @include media('<desktop') {
        max-width: initial;
        padding: 20px 30px 20px 0;
      }

      h1 {
        @include media('<widescreen') {
          font-size: 24px;
          margin-top: 0;
        }
        a{
          color: $black;
        }
      }

      p {
        margin: 25px 0 15px;
      }

      .support-phone {
        font-weight: bold;
        color: $text-color-base;
        text-decoration: none;

        &:hover {
          color: $black;
        }
      }
    }

    .home-right-block {
      @extend .list-reset;
      padding: 106px 0 30px;

      @include media('<widescreen') {
        padding-top: 71px;
      }

      li {
        & + li {
          margin: 40px 0 0 0;
        }

        p {
          color: lighten($black, 30%);
          margin: 0;
        }
      }
    }

    &.home-content {
      .right-block {
        span {
          text-transform: uppercase;
          font-weight: bold;
          font-size: 11px;
        }
      }
    }
  }

  /**
   * Invite confirmation
   */
  .trial-confirm {
    .trial-footer {
      margin: 15px -20px -20px;
      padding: 0 15px;

      border-top: 1px solid #eee;
      border-bottom-right-radius: 2px;
      border-bottom-left-radius: 2px;

      background-color: #FCFDFE;

      > .left, > .right {
        float: left;
        padding-top: $spacer * 3;
        padding-bottom: $spacer * 3;
        width: 50%;
        text-align: center;

        > i {
          color: $gray;
          font-size: 34px;
        }

        > p {
          margin-top: $spacer;
          margin-bottom: $spacer * 2;
          height: 34px;
          font-size: 12px;
        }
      }

      > .left {
        border-right: 1px solid #ddd;

        > p {
          line-height: 34px;
        }
      }
    }
  }

  .confirm-message {
    margin-top: $spacer * 3;
    margin-bottom: $spacer * 5;
    text-align: center;

    > i {
      color: $de-york;
      font-size: 80px;
    }

    > p {
      font-size: 16px;
      color: #666666;
    }
  }

  .panel-thank-you {
    display: inline-block;
    margin: auto;
    padding: 20px;

    .container {
      float: left;
      width: 420px;
      padding: 38px;
    }

    .list {
      text-align: left;
      position: relative;

      > i {
        position: absolute;
        left: -30px;

        &.icon-check-circle {
          color: $gray;
        }

        &.icon-envelope {
          color: $de-york;
        }
      }
    }
  }
}



